import React, { useEffect, useState } from 'react';
import { toast } from 'react-hot-toast';
import { ContaPagar, CreateContaPagarRequest, StatusContaPagar } from '@/services/api/contas-pagar.service';
import { fornecedorService, Fornecedor } from '@/services/api/fornecedor.service';
import Input from '@/components/form/input/InputField';
import Select from '@/components/ui/Select';
import Button from '@/components/ui/Button';
import DatePicker from '@/components/form/date-picker';
import { formatCurrency, formatCurrencyInput, parseCurrencyInput } from '@/utils/format';

type FormErrors = {
  nome?: string;
  observacao?: string;
  valor?: string;
  vencimento?: string;
  fornecedorId?: string;
  // numeroDocumento?: string;
};

type ContaPagarFormProps = {
  contaPagar?: ContaPagar | null;
  onSubmit: (data: CreateContaPagarRequest) => Promise<void>;
  onCancel: () => void;
};

const ContaPagarForm: React.FC<ContaPagarFormProps> = ({
  contaPagar,
  onSubmit,
  onCancel
}) => {
  const [formData, setFormData] = useState<CreateContaPagarRequest>({
    nome: contaPagar?.nome || '',
    observacao: contaPagar?.observacao || '',
    valor: contaPagar?.valor || 0,
    vencimento: contaPagar?.vencimento || new Date(),
    fornecedorId: contaPagar?.fornecedorId || 0,
    // numeroDocumento: contaPagar?.numeroDocumento || '',
    // observacoes: contaPagar?.observacoes || '',
    status: contaPagar?.status || 'PENDENTE'
  });

  const [valorFormatado, setValorFormatado] = useState(
    contaPagar?.valor ? formatCurrency(contaPagar.valor).replace('R$', '').trim() : ''
  );

  const [fornecedores, setFornecedores] = useState<Fornecedor[]>([]);
  const [isLoadingFornecedores, setIsLoadingFornecedores] = useState(true);
  const [errors, setErrors] = useState<FormErrors>({});
  const [isSubmitting, setIsSubmitting] = useState(false);

  useEffect(() => {
    loadFornecedores();
  }, []);

  const loadFornecedores = async () => {
    try {
      setIsLoadingFornecedores(true);
      const response = await fornecedorService.list({ page: 1, perPage: 100 });
      setFornecedores(response.data);
    } catch (error) {
      toast.error('Erro ao carregar fornecedores');
    } finally {
      setIsLoadingFornecedores(false);
    }
  };

  const validateForm = () => {
    const newErrors: FormErrors = {};
    const required = ['valor', 'vencimento'];

    required.forEach(field => {
      if (!formData[field as keyof typeof formData]) {
        newErrors[field as keyof FormErrors] = `${field.charAt(0).toUpperCase() + field.slice(1)} é obrigatório`;
      }
    });

    if (formData.valor <= 0) {
      newErrors.valor = 'Valor deve ser maior que zero';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!validateForm()) return;

    setIsSubmitting(true);
    try {
      await onSubmit(formData);
    } catch (error) {
      toast.error('Erro ao salvar conta a pagar');
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <div>
          <label htmlFor="observacao" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
            Nome
          </label>
          <Input
            id="nome"
            name="nome"
            value={formData.nome}
            onChange={(e: React.ChangeEvent<HTMLInputElement>) => setFormData({
              ...formData,
              nome: e.target.value
            })}
            error={errors.nome}
          />
        </div>

        {/* <div>
          <label htmlFor="observacao" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
            Descrição
          </label>
          <Input
            id="observacao"
            name="observacao"
            value={formData.observacao}
            onChange={(e: React.ChangeEvent<HTMLInputElement>) => setFormData({
              ...formData,
              observacao: e.target.value
            })}
            error={errors.observacao}
          />
        </div> */}

        <div>
          <label htmlFor="fornecedorId" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
            Fornecedor
          </label>
          <Select
            id="fornecedorId"
            name="fornecedorId"
            isLoading={isLoadingFornecedores}
            value={fornecedores
              .filter(f => f.id === formData.fornecedorId)
              .map(f => ({ value: String(f.id), label: f.nome }))[0]
            }
            options={fornecedores.map(f => ({
              value: String(f.id),
              label: f.nome
            }))}
            onChange={(option) => {
              if (option) {
                setFormData({
                  ...formData,
                  fornecedorId: parseInt((option as { value: string }).value, 10)
                });
              }
            }}
            placeholder="Selecione o fornecedor"
            error={errors.fornecedorId}
            className="w-full"
          />
        </div>

        <div>
          <label htmlFor="valor" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
            Valor
          </label>
          <div className="relative">
            <span className="absolute z-1 left-3 top-1/2 -translate-y-1/2 text-gray-500 dark:text-gray-300">
              R$
            </span>
            <Input
              id="valor"
              name="valor"
              type="text"
              value={valorFormatado}
              className="pl-9"
              onChange={(e) => {
                const input = e.target.value.replace(/\D/g, '');
                if (input === '') {
                  setValorFormatado('');
                  setFormData({ ...formData, valor: 0 });
                  return;
                }

                const formatted = formatCurrencyInput(input);
                setValorFormatado(formatted);
                setFormData({ ...formData, valor: parseCurrencyInput(input) });
              }}
              error={errors.valor}
            />
          </div>
        </div>

        <div>
          <label htmlFor="vencimento" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
            Data de Vencimento
          </label>
          <DatePicker
            id="vencimento"
            onChange={(selectedDates: Date[]) => {
              if (selectedDates[0]) {
                setFormData({
                  ...formData,
                  vencimento: selectedDates[0]
                });
              }
            }}
            defaultDate={new Date(formData.vencimento)}
          />
          {errors.vencimento && (
            <p className="mt-1 text-sm text-red-600">{errors.vencimento}</p>
          )}
        </div>

        {/* <div>
          <label htmlFor="numeroDocumento" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
            Número do Documento
          </label>
          <Input
            id="numeroDocumento"
            name="numeroDocumento"
            value={formData.numeroDocumento}
            onChange={(e: React.ChangeEvent<HTMLInputElement>) => setFormData({
              ...formData,
              numeroDocumento: e.target.value
            })}
            error={errors.numeroDocumento}
          />
        </div> */}
      </div>

      <div>
        <label htmlFor="observacoes" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
          Observações
        </label>
        <textarea
          id="observacao"
          name="observacao"
          rows={3}
          value={formData.observacao}
          onChange={(e) => setFormData({ ...formData, observacao: e.target.value })}
          className="w-full rounded-lg border border-stroke bg-transparent px-4 py-3 outline-none focus:border-primary focus-visible:shadow-none dark:border-gray-600 dark:bg-gray-900 dark:focus:border-primary dark:text-white"
        />
      </div>

      <div className="flex justify-end space-x-4 pt-4">
        <Button
          variant="secondary"
          onClick={onCancel}
          disabled={isSubmitting}
        >
          Cancelar
        </Button>
        <Button
          variant="primary"
          type="submit"
          disabled={isSubmitting}
        >
          {isSubmitting ? 'Salvando...' : contaPagar ? 'Atualizar' : 'Criar'}
        </Button>
      </div>
    </form>
  );
};

export default ContaPagarForm;
