"use client";

import React, { useEffect, useState, useCallback } from 'react';
import { toast } from 'react-hot-toast';
import Button from '@/components/ui/Button';
import { Modal } from '@/components/ui/modal';
import Input from '@/components/form/input/InputField';
import Select from '@/components/ui/Select';
import { ContaPagar, StatusContaPagar } from '@/services/api/contas-pagar.service';
import { contasPagarService } from '@/services/api/contas-pagar.service';
import ContaPagarCard from '@/components/contas/ContaPagarCard';
import Pagination from '@/components/ui/Pagination';
import ContaPagarForm from '@/components/contas/ContaPagarForm';
import Label from '@/components/form/Label';

type FilterOptions = {
  status?: StatusContaPagar;
  fornecedorId?: number;
  orderBy?: 'vencimento_asc' | 'vencimento_desc' | 'valor_asc' | 'valor_desc';
};

export default function ContasPagarPage() {
  const [contas, setContas] = useState<ContaPagar[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(0);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedConta, setSelectedConta] = useState<ContaPagar | null>(null);
  const [filters, setFilters] = useState<FilterOptions>({});
  const [itemsPerPage, setItemsPerPage] = useState(9);

  const loadContas = useCallback(async () => {
    try {
      setLoading(true);
      const response = await contasPagarService.list({
        page,
        perPage: itemsPerPage,
        search: searchTerm,
        status: filters.status ? [filters.status] : undefined,
        fornecedorId: filters.fornecedorId,
        orderBy: filters.orderBy
      });
      setContas(response.data);
      setTotalPages(response.pagination.pages);
    } catch (error) {
      toast.error('Erro ao carregar contas a pagar');
    } finally {
      setLoading(false);
    }
  }, [page, searchTerm, filters, itemsPerPage]);

  useEffect(() => {
    loadContas();
  }, [loadContas]);

  const handleSearch = (event: React.ChangeEvent<HTMLInputElement>) => {
    setSearchTerm(event.target.value);
    setPage(1);
  };

  const handleFilterChange = (newFilters: Partial<FilterOptions>) => {
    setFilters(prev => ({ ...prev, ...newFilters }));
    setPage(1);
  };

  const handleEdit = (conta: ContaPagar) => {
    setSelectedConta(conta);
    setIsModalOpen(true);
  };

  const handleDelete = async (id: number) => {
    if (!window.confirm('Tem certeza que deseja excluir esta conta?')) return;

    try {
      await contasPagarService.delete(id);
      toast.success('Conta excluída com sucesso');
      loadContas();
    } catch (error) {
      toast.error('Erro ao excluir conta');
    }
  };

  const handleStatus = async (conta: ContaPagar) => {
    try {
      await contasPagarService.update(conta.id, {
        ...conta,
        status: conta.status === 'PENDENTE' ? 'PAGO' : 'PENDENTE'
      });
      toast.success('Conta cancelada com sucesso');
      loadContas();
    } catch (error) {
      console.error('Erro ao cancelar conta', error);
      toast.error('Erro ao cancelar conta');
    }
  };

  const handleSave = async (data: any) => {
    try {
      if (selectedConta) {
        await contasPagarService.update(selectedConta.id, data);
        toast.success('Conta atualizada com sucesso');
      } else {
        await contasPagarService.create(data);
        toast.success('Conta criada com sucesso');
      }
      setIsModalOpen(false);
      setSelectedConta(null);
      loadContas();
    } catch (error) {
      toast.error('Erro ao salvar conta');
    }
  };

  const statusOptions = [
    { value: 'PENDENTE', label: 'Pendente' },
    { value: 'PAGO', label: 'Pago' },
    // { value: 'VENCIDO', label: 'Vencido' },
    // { value: 'CANCELADO', label: 'Cancelado' }
  ];

  const orderByOptions = [
    { value: 'vencimento_desc', label: 'Vencimento - Mais próximo' },
    { value: 'vencimento_asc', label: 'Vencimento - Mais distante' },
    { value: 'valor_desc', label: 'Valor - Maior' },
    { value: 'valor_asc', label: 'Valor - Menor' }
  ];

  return (
    <div className="p-4 sm:p-6 lg:p-8">
      <div className="mb-8">
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
          <div>
            <h1 className="text-2xl font-semibold text-gray-900 dark:text-white mb-2">
              Contas a Pagar
            </h1>
            <p className="text-sm text-gray-500">
              Gerencie todas as contas a pagar da empresa
            </p>
          </div>
          <div className="flex items-center gap-2">
            <Button
              variant="primary"
              onClick={() => {
                setSelectedConta(null);
                setIsModalOpen(true);
              }}
            >
              Nova Conta
            </Button>
          </div>
        </div>

        <div className="mt-6 flex flex-col sm:flex-row items-center gap-4">
          <div className="flex-1 w-full sm:max-w-xs">
            <Input
              type="text"
              placeholder="Buscar contas..."
              value={searchTerm}
              onChange={handleSearch}
              className="w-full"
            />
          </div>
          <div className="flex flex-wrap gap-3">
            <Select
              placeholder="Status"
              options={statusOptions}
              value={statusOptions.find(opt => opt.value === filters.status)}
              onChange={(option) => handleFilterChange({ 
                status: option ? (option as { value: StatusContaPagar }).value : undefined 
              })}
              isClearable
              className="whitespace-nowrap min-w-40"
            />
            <Select
              placeholder="Ordenar por"
              options={orderByOptions}
              value={orderByOptions.find(opt => opt.value === filters.orderBy)}
              onChange={(option) => handleFilterChange({ 
                orderBy: option ? (option as any).value : undefined 
              })}
              isClearable
              className="whitespace-nowrap min-w-50"
            />
          </div>
        </div>
      </div>

      <div className="flex justify-between mb-4 gap-2 flex-wrap">
        <div className='flex items-center gap-0.5'>
          <Label className='flex items-center mb-0 h-full'>Quantidade por página</Label>
          <Input
            className='max-w-24'
            defaultValue={itemsPerPage}
            max={100}
            min={1}
            onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
              const newValue = Number(e.target.value) || 9;
              setItemsPerPage(newValue);
              setPage(1);
            }}
          />
        </div>
        <Pagination
          currentPage={page}
          totalPages={totalPages}
          onPageChange={setPage}
        />
      </div>

      {loading ? (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {[1, 2, 3].map((n) => (
            <div key={n} className="animate-pulse bg-gray-100 dark:bg-gray-800 rounded-lg h-48" />
          ))}
        </div>
      ) : (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {contas.length > 0 ? (
            contas.map((conta) => (
              <ContaPagarCard
                key={conta.id}
                conta={conta}
                onEdit={() => handleEdit(conta)}
                onDelete={() => handleDelete(conta.id)}
                onHandleStatus={() => handleStatus(conta)}
                // onPagar={() => handlePagar(conta)}
                // onCancelar={() => handleCancelar(conta)}
              />
            ))
          ) : (
            <div className="col-span-full text-center py-12">
              <p className="text-gray-500 dark:text-gray-400">
                Nenhuma conta encontrada
              </p>
            </div>
          )}
        </div>
      )}

      {totalPages > 1 && (
        <div className="mt-6">
          <div className="flex justify-end mb-4 gap-2 flex-wrap">
            <Pagination
              currentPage={page}
              totalPages={totalPages}
              onPageChange={setPage}
            />
          </div>
        </div>
      )}

      <Modal
        isOpen={isModalOpen}
        onClose={() => {
          setIsModalOpen(false);
          setSelectedConta(null);
        }}
        className="max-w-[800px] m-4"
      >
        <div className="no-scrollbar relative w-full overflow-y-auto rounded-3xl bg-white p-4 dark:bg-gray-900 lg:p-11">
          <div className="px-2 pr-14">
            <h4 className="mb-2 text-2xl font-semibold text-gray-800 dark:text-white/90">
              {selectedConta ? 'Editar Conta' : 'Novo Conta'}
            </h4>
            <p className="mb-6 text-sm text-gray-500 dark:text-gray-400 lg:mb-7">
              {selectedConta 
                ? 'Atualize as informações do conta.' 
                : 'Preencha os dados para criar um novo conta.'
              }
            </p>
          </div>
          <ContaPagarForm
            contaPagar={selectedConta}
            onSubmit={handleSave}
            onCancel={() => {
              setIsModalOpen(false);
              setSelectedConta(null);
            }}
          />
        </div>
      </Modal>
    </div>
  );
}
