import { api, ApiResponse, PaginationResponse } from './config';

export type StatusContaPagar = 'PENDENTE' | 'PAGO';

export interface ContaPagar {
  id: number;
  nome: string; // Added 'nome' field
  observacao: string;
  valor: number;
  vencimento: Date;
  dataPagamento?: Date;
  status: StatusContaPagar;
  fornecedorId: number;
  categoriaId?: number;
  // numeroDocumento?: string;
  // observacoes?: string;
  fornecedor?: {
    id: number;
    nome: string;
  };
  categoria?: {
    id: number;
    nome: string;
  };
}

export interface CreateContaPagarRequest {
  nome: string; // Added 'nome' field
  observacao: string;
  valor: number;
  vencimento: Date;
  dataPagamento?: Date;
  status: StatusContaPagar;
  fornecedorId: number;
  categoriaId?: number;
  // numeroDocumento?: string;
  // observacoes?: string;
}

export interface UpdateContaPagarRequest extends Partial<CreateContaPagarRequest> {}

export const contasPagarService = {
  async create(data: CreateContaPagarRequest): Promise<ApiResponse<ContaPagar>> {
    const response = await api.post<ApiResponse<ContaPagar>>('/contas-pagar', data);
    return response.data;
  },

  async list({
    page = 1,
    perPage = 10,
    search = '',
    status,
    fornecedorId,
    categoriaId,
    dataInicio,
    dataFim,
    orderBy = 'vencimento_asc'
  }: {
    page?: number;
    perPage?: number;
    search?: string;
    status?: StatusContaPagar[];
    fornecedorId?: number;
    categoriaId?: number;
    dataInicio?: Date;
    dataFim?: Date;
    orderBy?: 'valor_asc' | 'valor_desc' | 'vencimento_asc' | 'vencimento_desc';
  } = {}): Promise<PaginationResponse<ContaPagar[]>> {
    const response = await api.get<PaginationResponse<ContaPagar[]>>('/contas-pagar', {
      params: {
        page,
        limit: perPage,
        search,
        status: status?.join(','),
        fornecedorId,
        categoriaId,
        dataInicio: dataInicio?.toISOString(),
        dataFim: dataFim?.toISOString(),
        orderBy
      }
    });
    return response.data;
  },

  async getById(id: number): Promise<ApiResponse<ContaPagar>> {
    const response = await api.get<ApiResponse<ContaPagar>>(`/contas-pagar/${id}`);
    return response.data;
  },

  async update(id: number, data: UpdateContaPagarRequest): Promise<ApiResponse<ContaPagar>> {
    const response = await api.put<ApiResponse<ContaPagar>>(`/contas-pagar/${id}`, data);
    return response.data;
  },

  async delete(id: number): Promise<ApiResponse<void>> {
    const response = await api.delete<ApiResponse<void>>(`/contas-pagar/${id}`);
    return response.data;
  },

  async registrarPagamento(id: number, dataPagamento: Date): Promise<ApiResponse<ContaPagar>> {
    const response = await api.post<ApiResponse<ContaPagar>>(`/contas-pagar/${id}/pagar`, {
      dataPagamento
    });
    return response.data;
  },

  async cancelarPagamento(id: number): Promise<ApiResponse<ContaPagar>> {
    const response = await api.post<ApiResponse<ContaPagar>>(`/contas-pagar/${id}/cancelar`);
    return response.data;
  }
};
