import React from 'react';
import { formatDate, formatCurrency } from '@/utils/format';
import { ContaPagar, StatusContaPagar } from '@/services/api/contas-pagar.service';
import Badge from '../ui/badge/Badge';
import ButtonCard from '../ui/button/ButtonCard';
import Button from '../ui/Button';

type ContaPagarCardProps = {
  conta: ContaPagar;
  onEdit: () => void;
  onDelete: () => void;
  // onPagar: () => void;
  // onCancelar: () => void;
  onHandleStatus?: () => void;
};

const statusColors = {
  PENDENTE: 'warning',
  PAGO: 'success',
} as const;

const ContaPagarCard: React.FC<ContaPagarCardProps> = ({
  conta,
  onEdit,
  onDelete,
  // onPagar,
  // onCancelar,
  onHandleStatus
}) => {
  return (
    <div className="bg-white dark:bg-gray-800 rounded-lg shadow-sm hover:shadow-md shadow-gray-200 dark:shadow-gray-700 border border-gray-200 dark:border-gray-700 p-4">
      <div>
        <div className="flex justify-between items-start mb-3">
          <div>
            <div className="flex items-center gap-2 flex-wrap overflow-x-hidden">
              <h3 className="font-medium text-gray-900 dark:text-white">
                {conta.nome}
              </h3>
              <Badge
                color={statusColors[conta.status]}
                className="capitalize"
              >
                {conta.status.toLowerCase().replace('_', ' ')}
              </Badge>
            </div>
            <p className="text-sm text-gray-500 dark:text-gray-400 mt-1">
              Valor: {formatCurrency(conta.valor)}
            </p>
            <p className="text-sm text-gray-500 dark:text-gray-400">
              Vencimento: {formatDate(conta.vencimento)}
            </p>
            {conta.dataPagamento && (
              <p className="text-sm text-gray-500 dark:text-gray-400">
                Data Pagamento: {formatDate(conta.dataPagamento)}
              </p>
            )}
            {conta.fornecedor && (
              <p className="text-sm text-gray-500 dark:text-gray-400">
                Fornecedor: {conta.fornecedor.nome}
              </p>
            )}
          </div>
          <div className="flex gap-2 flex-wrap">
            {/* <Button
              variant={conta.status === 'PENDENTE' ? "success" : "danger"}
              size="xs"
              onClick={onHandleStatus}
              title={conta.status === 'PENDENTE' ? "Registrar Pagamento" : "Reabrir Conta"}
            // icon="edit"
            >
              {conta.status === 'PENDENTE' ? "Pagar" : "Pendente"}
            </Button> */}

            <ButtonCard
              variant="secondary"
              size="xs"
              onClick={onEdit}
              title="Editar"
              icon="edit"
            />
            <ButtonCard
              variant="secondary"
              size="xs"
              onClick={onDelete}
              title="Excluir"
              icon="delete"
            />
          </div>
        </div>

        {/* {(conta.numeroDocumento || conta.observacoes) && (
          <div className="space-y-2">
            {conta.numeroDocumento && (
              <p className="text-sm text-gray-600 dark:text-gray-300">
                Documento: {conta.numeroDocumento}
              </p>
            )}
            {conta.observacoes && (
              <p className="text-sm text-gray-600 dark:text-gray-300 line-clamp-2">
                {conta.observacoes}
              </p>
            )}
          </div>
        )} */}
      </div>
    </div>
  );
};

export default ContaPagarCard;
